import './scss/style.scss'
import 'bootstrap'
import Swiper from 'swiper'
import data from './data/data'

$(() => {
    const menuItems = ['', ' ','',' '];
    const topGirls = [1 ,2, 3, 4, 5, 6, 7, 8];

    topGirls.map(top => {
        $('.swiper-wrapper').append(`<div class="swiper-slide"><img src="./img/top-girls/${top}.png" alt=""></div>`)
    });
    var swiper = new Swiper('.swiper-container', {
        slidesPerView: 3,
        spaceBetween: 10,
        freeMode: true
    });
    const isOnline = () => {

    };
    data.users.map((user, i) => {
        $('.girls-cards').append(`
            <div class="col-6">
                <div class="card">
                    <div class="card__img">
                        <img src="./img/girls-cards/${i+1}.png" alt="Girl photo">
                        <div class="photos-top-cont">
                            <div class="photos-top">
                                <div class="photos"><i class="fa fa-camera" aria-hidden="true"></i> <span class="photos${i}"></span></div>
                            </div>
                        </div>
                        <div class="fav-chat-cont">
                            <div class="fav-chat">
                                <div class="fav"><i class="fa fa-star-o" aria-hidden="true"></i><span> </span></div>
                                <div class="chat"><i class="fa fa-envelope-o" aria-hidden="true"></i><span> </span></div>
                            </div>
                        </div>
                    </div>
                    <div class="card__body">
                        <p><span class="username${i}"></span>, <span class="age${i}"></span></p>
                        <p><span class="city${i}"></span>, <span class="country${i}"></span></p>
                    </div>
               </div>
            </div>
        `);
        $('.username'+i).text(user.username);
        $('.age'+i).text(user.age);
        $('.city'+i).text(user.city);
        $('.country'+i).text(user.country);
        $('.photos'+i).text(user.photos);
        if (user.online) $('.age'+i).after(` <span class="online"></span>`);
        if (user.top) $('.photos').after(`<div class="top"><span class="top"></span></div>`)
    });
    /*menuItems.map(item => {
        $('.menu').append(`<li class="nav-item"><a class="nav-link" href="#">${item}</a></li>`);
    });*/
});





